const TOGGLE_MENU = 'TOGGLE_MENU';

export const toggleMenu = () => ({
  type: TOGGLE_MENU
});

const initialState = { open: false };

export const layoutReducer = (state = initialState, action) => {
  switch (action.type) {
    case TOGGLE_MENU:
      return { ...state, open: !state.open };
    default:
      return state;
  }
};
